#insurance-top {
  background: linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4)), url("../../assets/images/jean-pierre-brungs-qxCqkbF7tUc-unsplash.jpg");
    background-position: center ;
    background-repeat: no-repeat; /* Do not repeat the image */
    background-size: cover;
    padding: 50px;
  }
  
  #insurance-top-text {
      width: 40%;
      padding: 50px;
      /* backdrop-filter: blur(10px);
      border: 1px solid white;
      border-radius: 10px; */
    }
    
    #insurance-top-text h2 {
      color: white;
      font-size: 40px;
      font-weight: lighter;
    }
    
    #insurance-top-text p {
      font-size: 20px;
      color: white;
    }
  
    #insurance-contact-btn {
      background-color: transparent;
      border: 2px solid;
      border-radius: 5px;
      padding: 12px;
      color: white;
      font-weight: bold;
      font-size: 18px;
      margin-top: 40px;
    }
    
    #insurance-contact-btn a {
      text-decoration: none !important;
        color: white;
    }
    
    #insurance-contact-btn:hover {
      color: silver;
      background-color: silver;
      transition: 0.5s ease;
    }
  
    #insurance-logos {
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      align-items: center;
      padding: 50px;
    }
    
    #insurance-logos img {
      padding: 50px;
      height: 15%;
      width: 15%;
    }

      /* Extra small devices */
  @media only screen and (min-width: 320px) and (max-width: 479px) {

    #insurance-top-text {
      width: 75%;
    }

    #insurance-logos img {
      height: 70%; 
      width: 70%;
    }

  }

  /* Portrait tablets and large phones*/
@media only screen and (min-width: 480px) and (max-width: 768px) {

  #insurance-logos img {
    height: 40%; 
    width: 40%;
  }

}


/* Landscape tablets */
@media only screen and (min-width: 769px) and (max-width: 900px) {
  
  #insurance-logos img {
    height: 40%; 
    width: 40%;
  }
  
}